<template>
  <div class="classify" v-if="data">
    <!-- 顶部bar -->
    <TopBar></TopBar>
    <!-- <div class="classify-content">
      <van-tabs v-model="active" scrollspy sticky> 
        <van-tab title="男生" class="xxxxxxx">
          <ClassifyItem :male="male">男生</ClassifyItem>
        </van-tab>
        <van-tab title="女生">
          <ClassifyItem :female="female">女生</ClassifyItem>
        </van-tab>
        <van-tab title="出版">
          <ClassifyItem :press="press">出版</ClassifyItem>
        </van-tab>
        <van-tab title="标签">
          <ClassifyItem :picture="picture">标签</ClassifyItem>
        </van-tab>
      </van-tabs>
    </div>-->
    <cube-scroll-nav :data="tags" :side="true" @change="changeHandler">
      <cube-scroll-nav-panel v-for="item in tags" :key="item.tag" :label="item.tag">
        <van-grid :column-num="2">
          <van-grid-item v-for="(listitem, index) in data[item.name]" :key="index">
            <template slot="default">
              <div class="listitemcontent" @click="toClassifyDetail(listitem.name,item.name)">
                <div class="info">
                  <span class="title">{{ listitem.name }}</span>
                  <span class="count">{{ listitem.bookCount }}本</span>
                </div>
                <img style="width:75px;height:100px;" v-lazy="listitem.bookCover[0]" />
              </div>
            </template>
          </van-grid-item>
        </van-grid>
      </cube-scroll-nav-panel>
    </cube-scroll-nav>
  </div>
</template>

<script>
import TopBar from "@/components/TopBar.vue";
// import ClassifyItem from "@/components/ClassifyItem.vue";
import Vue from "vue";

export default {
  components: {
    TopBar
    // ClassifyItem
  },
  data() {
    return {
      male: null,
      female: null,
      picture: null,
      press: null,
      active: 0,
      data: null,
      tags: [
        { name: "male", tag: "男生" },
        { name: "female", tag: "女生" },
        { name: "press", tag: "出版" },
        { name: "picture", tag: "标签" }
      ],
      current: null
    };
  },
  watch: {
    male() {
      this.current = this.male[0].name;
      this.male.forEach(e => {
        e.bookCover[0] = unescape(e.bookCover[0].slice(7));
      });
    },
    female() {
      this.female.forEach(e => {
        e.bookCover[0] = unescape(e.bookCover[0].slice(7));
      });
    },
    picture() {
      this.picture.forEach(e => {
        e.bookCover[0] = unescape(e.bookCover[0].slice(7));
      });
    },
    press() {
      this.press.forEach(e => {
        e.bookCover[0] = unescape(e.bookCover[0].slice(7));
      });
    }
  },
  methods: {
    changeHandler(label) {
      window.console.log("changed to:", label);
    },
    stickyChangeHandler(current) {
      window.console.log("sticky-change", current);
    },
    toClassifyDetail(major, gender) {
      window.console.log(major, gender);
      this.$router.push({
        path: "classifyDetail",
        query: { major: major, gender: gender }
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    to,
      from,

    Vue.$myGet("classify", 60, "https://novel.kele8.cn/categories").then(
      res => {
        next(vm => {
          window.console.log(res);
          vm.data = res.data;
          ({ male: vm.male } = res.data);
          ({ female: vm.female } = res.data);
          ({ picture: vm.picture } = res.data);
          ({ press: vm.press } = res.data);
          vm.$toast.clear();
        });
      }
    );
  }
};
</script>

<style lang="scss">
.classify {
  .cube-scroll-wrapper {
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    // border-radius: 5px;
    height: 100vh;
    // height: 500px;
    overflow: visible;
    .cube-scroll-content {
      display: inline-block;
      .list-wrapper {
        padding: 0 10px;
        line-height: 60px;
        white-space: nowrap;
      }

      .list-item {
        display: inline-block;
      }
    }
    h2 {
      color: lightgray;
      text-align: center;
      &::before {
        content: "一一一一";
      }
      &::after {
        content: "一一一一";
      }
    }
    .cube-scroll-nav-panel {
      &:last-child {
        &::after {
          content: "";
          display: block;
          height: 200px;
        }
      }
    }
  }

  .van-grid {
    .van-grid-item {
      .listitemcontent {
        display: flex;
        line-height: 40px;
        .info {
          width: 60px;
          display: flex;
          flex-direction: column;
          span {
            height: 20px;
            &.title {
              font-size: 12px;
            }
            &.count {
              font-size: 10px;
              color: rgb(175, 175, 175);
            }
          }
        }

        .van-image {
          flex: 1;
        }
      }
    }
  }
}
</style>
