import Vue from 'vue'
import './cube-ui'
import './plugins/axios'
import App from './App.vue'
import router from './router'
import store from './store'
import './global'
import './vant'
import './cube'
import VueLazyload from 'vue-lazyload'
import  FastClick  from  'fastclick'


FastClick.attach(document.body);
//懒加载配置
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: require('@/assets/error.jpg'),
  loading: require('@/assets/error.jpg'),
  attempt: 1
})

Vue.config.productionTip = false

/**
 * 自定义的ajax请求
 * @param {string} key 设置localstorage名字
 * @param {string} expireTime 过期时间，单位为分
 * @param {string} url ajax请求url
 * @param {object} data 参数,可以省略
 */

function $myGet(key, expireTime,url, data = {}) {
  return new Promise((resolve, reject) => {
    let stor = JSON.parse(localStorage.getItem(key));
    if (stor && Date.now() < stor.time) {
      resolve(stor)
    } else {
      this.axios.get(url, data)
        .then(res => {
          resolve(res)
          let msg = res.data
          localStorage.setItem(
            key,
            JSON.stringify({
              time: Date.now() + 60 * 1000 * expireTime,
              data: msg
            })
          );
        }).catch(err => {
          reject(err)
        })
    }
  })
}
Vue.$myGet = $myGet
Vue.prototype.$myGet = $myGet
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')