<template>
  <div class="long-comment">
    <van-panel desc="描述信息" v-if="longComments">
      <div slot="header" class="header">
        <div class="title">
          热门书评<span>{{ longComments.length }}条</span>
        </div>
        <div class="right"><van-icon name="edit" />写书评</div>
      </div>
      <CommentItem
        v-for="(item, index) in someLongComments"
        :key="index"
        :comment="item"
      ></CommentItem>
      <!-- ------------------------------- -->

      <div class="todetail">查看全部{{ longComments.length }}条书评</div>
    </van-panel>
  </div>
</template>

<script>
import CommentItem from "@/components/CommentItem.vue";

export default {
  props: ["longComments"],
  components: {
    CommentItem
  },
  data() {
    return {
      someLongComments: null
    };
  },
  watch: {
    longComments() {
      this.getSomelongComments();
    }
  },
  methods: {
    getSomelongComments() {
      if (this.longComments) {
        this.someLongComments = this.longComments.slice(0, 3);
      }
    }
  },
  created() {
    this.getSomelongComments();
  }
};
</script>

<style lang="scss">
.long-comment {
  background: #fff;
  margin-top: 5px;
  padding: 0 16px;
  width: 100vw;
  box-sizing: border-box;
  overflow: hidden;
  .van-panel {
    .header {
      overflow: hidden;
      line-height: 36px;
      font-size: 14px;
      border-bottom: 1px solid #e6e4e4;
      .title {
        float: left;
        color: #585858;
        font-weight: bold;
        span {
          font-size: 12px;
          margin-left: 5px;
        }
      }
      .right {
        float: right;
        color: #c42c2c;
        font-size: 12px;
        .van-icon {
          vertical-align: middle;
        }
      }
    }
    .comment-item {
      .default {
        // padding: 10px 0;
        border-top: 1px solid #e6e4e4;
        .nick-info {
          padding: 10px 0 0;
          .pic {
          }
          span {
            color: #646566;
            font-size: 12px;
          }
          .van-tag {
            font-size: 10px;
            margin-left: 5px;
          }
        }
        .score {
          padding: 5px 0;
          span {
            font-size: 10px;
            color: #646566;
            margin-left: 5px;
          }
        }
        .comment-content {
          padding: 10px 0 5px 0;
          color: #646566;
          font-size: 12px;
        }
      }

      .footer {
        display: flex;
        font-size: 10px;
        color: #abacad;
        line-height: 24px;
        .footer-left {
          flex: 1;
        }
        .footer-right {
          width: 50px;
          .van-icon {
            vertical-align: middle;
          }
        }
      }
    }
    .todetail {
      border-top: 1px solid #e6e4e4;
      text-align: center;
      line-height: 36px;
      color: #c42c2c;
    }
  }
}
</style>
