<template>
  <div class="book-detail" v-if="bookDetails">
    <!-- 顶部bar -->
    <van-nav-bar
      :fixed="true"
      :title="$route.name"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    >
      <van-icon name="comment-o" slot="right" />
      <van-icon name="share" slot="right" />
    </van-nav-bar>
    <!-- 顶部面板 -->
    <div class="book-detail-top">
      <!-- 图片 书名等info -->
      <div class="book-detail-top-info">
        <van-panel>
          <!-- header为空清除默认 -->
          <div slot="header" style="height:70px;"></div>
          <van-row type="flex">
            <van-col span="6" offset="2">
              <van-image width="75" height="100" :src="bookDetails.cover" />
            </van-col>
            <van-col span="16">
              <div class="title">{{ bookDetails.title }}</div>
              <div class="author">
                <span>{{ bookDetails.author }}</span>
                <span>{{ bookDetails.copyright }}</span>
              </div>
              <div class="word">
                <span>{{ bookDetails.minorCate }}</span>
                <span>{{ translatePlayCount(bookDetails.wordCount) }}字</span>
              </div>
            </van-col>
          </van-row>
        </van-panel>
      </div>
      <!-- 书籍评价 留存 人气信息等data -->
      <div class="book-detail-top-data">
        <van-row>
          <van-col span="9" class="score">
            <!-- 评分 -->
            <p>
              <span>{{ bookDetails.rating.score }}</span>
              <van-rate
                v-model="value"
                allow-half
                void-icon="star"
                void-color="#eee"
                size="10"
                :readonly="true"
              />
            </p>
            <p>{{ bookDetails.rating.tip }}</p>
          </van-col>
          <van-col span="5" class="retentionRatio">
            <p>{{ bookDetails.retentionRatio }}%</p>
            <p>读者留存</p>
          </van-col>
          <van-col span="5" class="latelyFollower">
            <p>{{ translatePlayCount(bookDetails.latelyFollower) }}</p>
            <p>七日人气</p>
          </van-col>
          <van-col span="5" class="totalFollower">
            <p>{{ translatePlayCount(bookDetails.totalFollower) }}</p>
            <p>累计人气</p>
          </van-col>
        </van-row>
      </div>
    </div>
    <!-- 简介 -->
    <div class="summary">
      <van-panel>
        <template slot="header">
          <div class="header">简介</div>
        </template>
        <!-- tags -->
        <van-tag round color="#F2F2F2" v-for="(item, index) in bookDetails.tags" :key="index">
          {{
          item
          }}
        </van-tag>
        <!-- <div class="longIntro"></div> -->
        <div class="longIntro" @click="longIntroHide = !longIntroHide">
          <pre :class="{ hide: longIntroHide }">{{ bookDetails.longIntro }}</pre>
          <van-icon v-if="longIntroHide" name="arrow-down" />
        </div>
        <div class="catalogue" @click="toCatalogue(bookDetails._id,bookDetails.title)">
          <span class="catalogue-title">目录</span>
          <div class="catalogue-info">
            <span class="updated">{{ bookDetails.updated }}</span>
            <span class="chapter">{{ bookDetails.lastChapter }}</span>
          </div>
          <van-icon class="arrow-right" name="arrow" />
        </div>
      </van-panel>
    </div>
    <!-- 评论组件 -->
    <ShortComment :short-comments="shortComments"></ShortComment>
    <LongComment :long-comments="longComments"></LongComment>
    <!-- 填底部的高度 -->
    <div class="bottom-empty"></div>
    <!-- 底部bar -->
    <van-tabbar class="bookdetail-bottom-bar">
      <van-tabbar-item @click="addToBookcase()" v-if="!isExist">
        <van-icon name="plus" />追更新
      </van-tabbar-item>
      <van-tabbar-item class="cross" @click="removeFromBookcase()" v-else>
        <van-icon name="cross" />不追了
      </van-tabbar-item>
      <van-tabbar-item @click="toReadPage()">开始阅读</van-tabbar-item>
      <!-- <van-tabbar-item><van-icon :name="downloadImg" />下载</van-tabbar-item> -->
    </van-tabbar>
  </div>
</template>

<script>
import Vue from "vue";
import ShortComment from "@/components/ShortComment.vue";
import LongComment from "@/components/LongComment.vue";
export default {
  components: {
    ShortComment,
    LongComment
  },
  data() {
    return {
      value: 0,
      bookId: "",
      bookDetails: null,
      longIntroHide: true,
      bookName: "",
      downloadImg: require("@/assets/download.png"),
      shortComments: null,
      longComments: null,
      isExist: false,
      currentChapterIndex:0
    };
  },
  watch: {
    // bookName() {
    //   this.getBookId(this.bookName);
    // },
    // bookId() {
    //   this.getBookDetails();
    // },
    bookDetails() {
      this.bookDetails.cover = unescape(this.bookDetails.cover.slice(7));
      this.value = this.bookDetails.rating.score / 2;
      this.timeMaker(this.bookDetails.updated);
      this.bookId = this.bookDetails._id;
      //   this.$nextTick(()=>{
      //       window.console.log(this.$el)
      //       this.$el.querySelector(".summary .longIntro").innerHTML = this.bookDetails.longIntro
      //   })
    },
    bookId() {
      this.checkBookcase();
    }
  },
  methods: {
    //检查该书籍是否在书架中的方法
    checkBookcase() {
      window.console.log("书籍详情", this.bookDetails);
      var stor = JSON.parse(localStorage.getItem("bookcase"));
      window.console.log(stor);
      window.console.log(this.bookDetails._id);
      if (stor) {
        stor.forEach(e => {
          if (e.id == this.bookDetails._id) {
            window.console.log("书籍已经存在");
            this.currentChapterIndex = e.currentChapterIndex
            this.isExist = true;
          }
        });
      }
    },
    //移出书架的方法
    removeFromBookcase() {
      let id = this.bookDetails._id;
      window.console.log("要移除的书籍id", id);
      var stor = JSON.parse(localStorage.getItem("bookcase"));
      this.isExist = false;
      stor.forEach((e, i, arr) => {
        if (e.id == id) {
          window.console.log("找到该书籍并删除");
          arr.splice(i, 1);
          window.console.log(i, arr);
          localStorage.setItem("bookcase", JSON.stringify(arr));
          this.checkBookcase();
        }
      });
    },
    addToBookcase() {
      let id = this.bookDetails._id;
      window.console.log(id);
      let stor = JSON.parse(localStorage.getItem("bookcase"));
      // 一进来应该先遍历该书ID是否在书架中，在的话就让加入书架按钮变成移出书架按钮
      window.console.log(stor);
      // 如果书架有书，向后面追加
      if (stor) {
        stor.forEach((e, i) => {
          if (id == e.id) {
            window.console.log("已经在书架");
          }
        });
        if (!this.isExist) {
          let newBook = {
            id: id,
            title: this.bookDetails.title,
            updated: this.bookDetails.updated,
            currentChapterIndex: 0,
            bookDetails: this.bookDetails
          };
          stor.unshift(newBook);
          localStorage.setItem("bookcase", JSON.stringify(stor));
        }
      } else {
        // 如果书架一本书都没，则新添加
        window.console.log(this.bookDetails);
        let arr = [];
        let newBook2 = {
          id: id,
          title: this.bookDetails.title,
          updated: this.bookDetails.updated,
          currentChapterIndex: 0,
          bookDetails: this.bookDetails
        };
        arr.unshift(newBook2);
        localStorage.setItem("bookcase", JSON.stringify(arr));
      }
      this.checkBookcase();
    },
    toReadPage() {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      //拿书源id
      this.axios
        .get(
          "https://novel.kele8.cn/book-sources?view=summary&book=" +
            this.bookDetails._id
        )
        .then(res => {
          window.console.log(res.data[0]._id);
          //根据书源id拿章节列表
          this.axios
            .get("https://novel.kele8.cn/book-chapters/" + res.data[0]._id)
            .then(response => {
              window.console.log(response);
              let { chapters } = response.data;
              this.$store.commit("setChapters", chapters);
              //默认第一章
              let { link } = chapters[this.currentChapterIndex];
              this.$router.push({
                path: "readpage",
                query: { link: link, title: this.bookDetails.title,id: this.bookDetails.id }
              });
            });
        });
      // this.$router.push({path:'readpage',query:{link:link,title:this.title}})
    },

    toCatalogue(id, title) {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      this.axios
        .get(this.$origin+"/book-sources?view=summary&book=" + id)
        .then(res => {
          this.$router.push({
            path: "catalogue",
            query: { id: res.data[0]._id, title: title }
          });
        });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    timeMaker(t) {
      var date = new Date(t); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var now = new Date();

      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate();
      var h = date.getHours();
      var m = date.getMinutes();
      var s = date.getSeconds();
      var NY = now.getFullYear();
      var NM =
        now.getMonth() + 1 < 10
          ? "0" + (now.getMonth() + 1)
          : now.getMonth() + 1;
      var ND = now.getDate();
      var Nh = now.getHours();
      var Nm = now.getMinutes();
      var Ns = now.getSeconds();

      if (NY - Y > 0) {
        this.bookDetails.updated = NY - Y + "年前更新";
      } else if (NM - M > 0) {
        this.bookDetails.updated = NM - M + "月前更新";
      } else if (ND - D > 0) {
        this.bookDetails.updated = ND - D + "天前更新";
      } else if (Nh - h > 0) {
        this.bookDetails.updated = Nh - h + "小时前更新";
      } else if (Nm - m > 0) {
        this.bookDetails.updated = Nm - m + "分前更新";
      } else if (Ns - s > 0) {
        this.bookDetails.updated = Ns - s + "秒前更新";
      }
      // this.bookDetails.updated = Y + M + D + h + m + s;
    },
    // getBookId(name) {
    //   this.axios
    //     .get(this.$origin+"/search", {
    //       params: {
    //         keyword: name
    //       }
    //     })
    //     .then(res => {
    //       window.console.log(res);
    //       this.bookId = res.data.books[0]._id;
    //     });
    // },
    // getBookDetails() {
    //   this.axios.get(this.$origin+"/book-info/" + this.bookId).then(res => {
    //     this.bookDetails = res.data;
    //   });
    // },
    translatePlayCount(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(2) + "亿";
      } else if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      } else {
        return n;
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this.checkBookcase();
    });
    // window.console.log(this.$route);
    // this.bookName = this.$route.query.bookName;
    // this.getBookId(this.bookName);
    //获取短评
    this.axios
      .get(this.$origin+"/book/short-reviews", {
        params: {
          book: this.$route.query.bookId
        }
      })
      .then(res => {
        window.console.log(res);
        this.shortComments = res.data.docs;
      });

    //获取书评
    this.axios
      .get(this.$origin+"/book/reviews", {
        params: {
          book: this.$route.query.bookId
        }
      })
      .then(res => {
        window.console.log(res);
        this.longComments = res.data.reviews;
      });
  },
  // 这个方法不用调用this,因为此时机组件还没生成,所以无this
  // 要调用刷新数据方法时,需要写在next回调里,
  // next是在组件生成后会调用
  //进入页面前先请求数据 请求成功显示页面
  beforeRouteEnter(to, from, next) {
    Vue.axios
      .get("https://novel.kele8.cn/book-info/" + to.query.bookId)
      .then(res => {
        next(vm => {
          vm.$toast.clear();
          vm.bookDetails = res.data;
          vm.isExist = false;
        });
      })
      .catch(() => {
        // next(vm=>{
        //如果请求失败应该给个友好提示
        // })
      });

    // next(vm => {
    //   // 通过 `vm` 访问组件实例
    //   window.console.log("xx", vm.$route.query.bookName);
    //   vm.bookName = vm.$route.query.bookName;
    // });
  },
  // 这个方法是路由不变,只变参数时执行
  beforeRouteUpdate(to, from, next) {
    this.isExist = false;
    next();
  }
};
</script>

<style lang="scss">
@mixin threeline() {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.book-detail {
  background: #edeef0;
  //顶部bar
  .van-nav-bar {
    .van-icon {
    }
  }
  //顶部面板
  .book-detail-top {
    .book-detail-top-info {
      //    图片 书名等info
      .van-panel {
        background: rgb(80, 80, 80);
        .van-row {
          padding: 0;
          .van-col {
            .van-image {
              vertical-align: middle;
              box-shadow: 0 0 12px -1px rgba(0, 0, 0, 0.4);
            }
            div {
              margin: 5px 0;
            }
            .title {
              color: #fff;
            }
            @mixin topspan() {
              span {
                font-size: 12px;
                display: inline-block;
                &:nth-child(1) {
                  color: #ddbb6d;
                }
                &:nth-child(2) {
                  color: #d1d1d1;
                  font-size: 8px;
                  &::before {
                    padding: 5px;
                    content: "|";
                  }
                }
              }
            }
            .author {
              @include topspan();
            }
            .word {
              font-size: 12px;
              color: #d1d1d1;
              @include topspan();
            }
          }
        }
      }
    }
    .book-detail-top-data {
      //   书籍评价 留存 人气信息
      padding-top: 7px;
      background: #fff;
      .van-row {
        padding: 10px;
        .van-col {
          font-size: 14px;
          color: #a1a1a1;
          font-family: "正楷";
          p {
            padding: 0;
            margin: 0;

            &:nth-child(2) {
              margin: 5px 0;
              font-size: 8px;
              color: #b6b6b6;
            }
          }
          &.score {
            p {
              span {
                margin-right: 5px;
              }
            }
          }
          &.retentionRatio {
          }
          &.latelyFollower {
          }
          &.totalFollower {
          }
        }
      }
    }
  }

  //简介
  .summary {
    .van-panel {
      padding: 0 16px;
      .header {
        font-size: 14px;
        color: #585858;
        font-weight: bold;
        padding: 5px 0;
      }
      .van-cell {
        padding: 0;
        color: #646566;
      }
      .van-tag {
        color: #899098;
        padding: 3px 7px;
        font-family: "正楷";
        margin: 8px 5px 10px 0;
      }
      .longIntro {
        position: relative;
        padding-bottom: 20px;
        pre {
          font-family: "正楷";
          letter-spacing: 1px;
          color: #686868;
          font-size: 12px;
          margin-top: 0;
          white-space: pre-wrap; /*css-3*/
          white-space: -moz-pre-wrap; /*Mozilla,since1999*/
          white-space: -pre-wrap; /*Opera4-6*/
          white-space: -o-pre-wrap; /*Opera7*/
          word-wrap: break-word; /*InternetExplorer5.5+*/
          &.hide {
            @include threeline();
          }
        }
        .van-icon {
          position: absolute;
          bottom: 0;
          right: 0;
          padding-left: 5px;
          background: rgba(255, 255, 255, 0.7);
        }
      }
      .catalogue {
        border-top: 1px solid rgb(230, 228, 228);
        padding: 5px 0;
        display: flex;
        line-height: 32px;
        div:nth-child(1) {
          flex: none;
        }
        .catalogue-title {
          width: 100px;
          color: #585858;
          font-weight: bold;
          font-size: 14px;
        }
        .catalogue-info {
          flex: 1;
          @include threeline();
          -webkit-line-clamp: 1;
          .updated {
            font-size: 10px;
            color: #c42c2c;
          }
          .chapter {
            font-size: 10px;
            color: #9e9da2;
          }
        }
        .arrow-right {
          line-height: 32px;
        }
      }
    }
  }
  .bottom-empty {
    height: 50px;
  }
  .bookdetail-bottom-bar {
    .van-tabbar-item {
      background: #fff;
      color: #db2020;
      font-size: 18px;
      &:nth-child(2) {
        background: #db2020;
        color: #fff;
      }
      .van-icon {
        vertical-align: top;
        color: #db2020;
      }
    }
    .van-tabbar-item.cross {
      background: #ddd;
      color: #7e6f6f;
      font-size: 18px;

      .van-icon {
        vertical-align: top;
        color: #7e6f6f;
      }
    }
  }
}
</style>
