<template>
  <div class="catalogue">
    <TopBar :title="title"></TopBar>

    <!-- 目录列表 -->
    <div class="scroll-list-wrap">
      <cube-scroll ref="scroll">
        <van-list>
          <van-cell v-for="(item, index) in chapters" :key="index" @click="toReadPage(item.link)">
            <template slots="default">
              <span class="index">{{ index+1 }}.</span>
              <span class="chapter">{{ item.title }}</span>
            </template>
          </van-cell>
        </van-list>
      </cube-scroll>
    </div>
  </div>
</template>

<script>
import TopBar from "@/components/TopBar.vue";
import Vue from "vue";
export default {
  components: {
    TopBar
  },
  data() {
    return {
      chapters: null,
      title:null
    };
  },
  methods: {
    toReadPage(link){
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      this.$router.push({path:'readpage',query:{link:link,title:this.title}})
    }
  },
  watch: {
    chapters(){
      this.$store.commit("setChapters",this.chapters)
    }
  },
  beforeRouteEnter(to, from, next) {
    // ...
    Vue.axios.get(Vue.$origin+"/book-chapters/" + to.query.id).then(res => {
      next(vm => {
        ({ chapters: vm.chapters } = res.data);
        vm.$toast.clear();
        vm.title = to.query.title
      });
    });
  }
};
</script>

<style lang="scss">
.catalogue {
  .scroll-list-wrap {
    height: 10px;
    overflow: visible;
    .cube-scroll-wrapper {
      overflow: visible;
    }
    .van-list {
      .van-cell {
        span {
          display: inline-block;
          color: #8E8E8E;
          &.index {
            width: 40px;
          }
          &.chapter {
          }
        }
      }
    }
  }
}
</style>
