<template>
  <div class="topbar">
    <!-- 顶部bar -->
    <van-nav-bar
    class="topbar-bar"
      :fixed="true"
      :title="title?title:$route.name"
      @click-left="onClickLeft"
    >
        <template slot="left">
            <van-icon color="red" name="arrow-left" />
            <span>返回</span>
        </template>
    </van-nav-bar>
    <div class="empty"></div>
  </div>
</template>

<script>
export default {
  props:["title"],
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss">
.topbar{
    .van-nav-bar{
        &.topbar-bar{
          position: absolute;
          z-index: 999 !important;
          background: #fff;
            .van-icon{}
            span{
                color: red;
            }
        }
    }
    .empty {
    //因为固定定位TopBar不占位置，所以需要把上面的位置顶出来
    height: 46px;
  }
}
</style>
