<template>
  <div class="read-page" :class="{ night: isNight }">
    <!-- 可隐藏头部 -->
    <div
      class="topbar"
      :class="{ hide: showBar }"
      :style="{ backgroundColor: backgroundColor, color: textColor }"
    >
      <div class="topbar-left">
        <van-icon size="24" name="arrow-left" @click="$router.go(-1)" />
        <span>{{ $route.query.title }}</span>
      </div>
      <div class="topbar-right">
        <van-icon name="description" />
        <van-icon name="ellipsis" />
      </div>
    </div>
    <template v-if="chapter">
      <!-- 固定头部 显示章节信息 -->
      <div
        class="novel-top"
        :style="{ backgroundColor: backgroundColor, color: textColor }"
      >{{ chapter.title }}</div>
      <!-- 文章 -->
      <div
        class="scroll-list-wrap"
        @click="
          showBar = !showBar;
          showset = false;
        "
        v-if="!isTurn"
      >
        <cube-scroll ref="scroll">
          <div
            class="novel-content"
            :style="{ backgroundColor: backgroundColor, color: textColor, fontFamily: fontfamily }"
          >
            <p
              :style="{ lineHeight: lineheight, fontSize: fontSize }"
              v-for="(item, index) in chapter.cpContent"
              :key="index"
            >{{ item }}</p>
          </div>
        </cube-scroll>
      </div>
      <!-- 文章 -->
      <!-- 以下为翻页模式 -->
      <div
        class="novel-content"
        v-if="isTurn"
        :style="{ backgroundColor: backgroundColor, color: textColor, fontFamily: fontfamily }"
        @click="turnPage"
      >
        <div class="turn" ref="turn">
          <div class="content" ref="content">
            <p
              :style="{ lineHeight: lineheight, fontSize: fontSize }"
              v-for="(item, index) in chapter.cpContent"
              :key="index"
            >{{ item }}</p>
          </div>
        </div>
        <div
          class="midBtn"
          @click.stop="
          showBar = !showBar;
          showset = false;
        "
        ></div>
      </div>
    </template>
    <!-- 可隐藏底部bar -->
    <van-tabbar
      class="read-page-bottom-bar"
      :class="{ hide: showBar }"
      :style="{ backgroundColor: backgroundColor, color: textColor }"
    >
      <!-- <span>目录</span> -->
      <van-tabbar-item replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? listIcon.active : listIcon.inactive"
          v-if="!isNight"
          @click="showCatalogue"
        />
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? lightlistIcon.active : lightlistIcon.inactive"
          v-else
          @click="showCatalogue"
        />
      </van-tabbar-item>
      <!-- <span>夜间模式</span> -->
      <van-tabbar-item replace @click="toNight">
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? nightIcon.active : nightIcon.inactive"
          v-if="!isNight"
        />
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? sunIcon.active : sunIcon.inactive"
          v-else
        />
      </van-tabbar-item>
      <!-- <span>设置</span> -->
      <van-tabbar-item replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? setIcon.active : setIcon.inactive"
          v-if="!isNight"
          @click="showSet"
        />
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? lightsetIcon.active : lightsetIcon.inactive"
          v-else
          @click="showSet"
        />
      </van-tabbar-item>
    </van-tabbar>
    <!-- 目录弹出 -->
    <van-popup v-model="showcatalogue" position="left" :style="{ width: '80%', height: '100%' }">
      <van-list>
        <van-cell
          v-for="(item, index) in chapters"
          :key="index"
          @click="changeChapter(item.link,index)"
        >
          <template slots="default">
            <div class="chapter-item" :class="{ active: currentChapterIndex==index }">
              <span class="index">{{ index + 1 }}.</span>
              <span class="chapter">{{ item.title }}</span>
            </div>
          </template>
        </van-cell>
      </van-list>
    </van-popup>
    <!-- 设置弹出 -->
    <van-popup
      class="set"
      v-model="showset"
      position="bottom"
      :overlay="false"
      :style="{ height: '35%', backgroundColor: backgroundColor, color: textColor }"
    >
      <!-- 亮度设置 -->
      <div class="light">
        <img class="small" src="@/assets/sun.png" alt />
        <van-slider
          v-model="lightValue"
          bar-height="4px"
          active-color="#ee0a24"
          :min="10"
          :max="100"
          :step="1"
        />
        <img class="big" src="@/assets/sun.png" alt />
      </div>
      <!-- 字体大小设置 -->
      <div class="textsize">
        <button class="minus" @click="minus">Aa-</button>
        <van-stepper
          v-model="textsize"
          :disable-input="true"
          :show-plus="false"
          :show-minus="false"
        />
        <button class="plus" @click="plus">Aa+</button>
      </div>
      <!-- 颜色设置 -->
      <!-- 每个元素的两侧间隔相等 -->
      <van-row class="color" type="flex" justify="space-around">
        <van-col span="4" @click="(backgroundColor = '#f7f1de'), (isNight = false)">
          <div></div>
        </van-col>
        <van-col span="4" @click="(backgroundColor = '#f4deb5'), (isNight = false)">
          <div></div>
        </van-col>
        <van-col span="4" @click="(backgroundColor = '#DBC8A8'), (isNight = false)">
          <div></div>
        </van-col>
        <van-col span="4" @click="(backgroundColor = '#d1f0d0'), (isNight = false)">
          <div></div>
        </van-col>
        <van-col span="4" @click="(backgroundColor = '#FFE7D9'), (isNight = false)">
          <div></div>
        </van-col>
      </van-row>
      <!-- 行高设置 -->
      <van-row class="lineheight" type="flex" justify="space-around">
        <van-col span="4" @click="lineheight = '24px'">
          <img src="@/assets/line-spacing-s.png" alt />
        </van-col>
        <van-col span="4" @click="lineheight = '26px'">
          <img src="@/assets/line-spacing-m.png" alt />
        </van-col>
        <van-col span="4" @click="lineheight = '28px'">
          <img src="@/assets/line-spacing-l.png" alt />
        </van-col>
      </van-row>
      <!-- 字体设置 -->
      <van-row class="fontfamily" type="flex" justify="space-around">
        <van-col span="4" @click="fontfamily = '宋体'">
          <div>宋体</div>
        </van-col>
        <van-col span="4" @click="fontfamily = '仿宋'">
          <div>仿宋</div>
        </van-col>
        <van-col span="4" @click="fontfamily = '正楷'">
          <div>正楷</div>
        </van-col>
        <van-col span="4" @click="fontfamily = '隶书'">
          <div>隶书</div>
        </van-col>
        <van-col span="4" @click="fontfamily = '黑体'">
          <div>黑体</div>
        </van-col>
      </van-row>
    </van-popup>
    <van-overlay :show="show" :style="{ opacity: (100 - lightValue) / 100 }">
      <div class="wrapper">
        <!-- <div class="block" /> -->
      </div>
    </van-overlay>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  data() {
    return {
      chapter: null,
      showBar: true,
      showcatalogue: false,
      showset: false,
      isNight: false,
      chapters: null,
      link: null,
      lightValue: 70,
      backgroundColor: "#f2eeeb",
      textColor: "#000000",
      lineheight: "24px",
      textsize: 16,
      fontSize: "16px",
      show: true,
      fontfamily: "正楷",
      isTurn: true,
      columnCount: 0,
      currentColumn: 0,
      th: 0,
      currentChapterIndex: 0,
      currentX: 0,
      // brightness:"rgba(0,0,0,0.3)",
      listIcon: {
        active: require("@/assets/list.png"),
        inactive: require("@/assets/list.png")
      },
      lightlistIcon: {
        active: require("@/assets/lightlist.png"),
        inactive: require("@/assets/lightlist.png")
      },
      nightIcon: {
        active: require("@/assets/night.png"),
        inactive: require("@/assets/night.png")
      },
      setIcon: {
        active: require("@/assets/set.png"),
        inactive: require("@/assets/set.png")
      },
      lightsetIcon: {
        active: require("@/assets/lightset.png"),
        inactive: require("@/assets/lightset.png")
      },
      sunIcon: {
        active: require("@/assets/sun.png"),
        inactive: require("@/assets/sun.png")
      },
      bookId: ""
    };
  },
  watch: {
    // 字体改变时 页数可能会变 监听变化
    textsize() {
      this.$nextTick(() => {
        this.getColumnCount();
      });
    },
    currentX() {
      this.$refs.turn.style.transform = `translateX(-${this.currentX}px)`;
    },
    chapter() {
      window.console.log(this.chapter.cpContent.split("\n"));
      let x = this.chapter.cpContent.split("\n").filter(v => {
        return v !== " ";
      });
      x.forEach((e, i) => {
        x[i] = e.trim();
      });
      this.chapter.cpContent = x;
      window.console.log(this.$refs.content.clientHeight);
    },
    isNight() {
      if (this.isNight) {
        this.backgroundColor = "#141414";
        this.textColor = "#686868";
      } else {
        this.backgroundColor = "#f2eeeb";
        this.textColor = "#000000";
      }
    }
  },
  methods: {
    //保存当前章节
    saveCurrentChapterIndex() {
      let id = this.bookId;
      var stor = JSON.parse(localStorage.getItem("bookcase"));
      window.console.log(stor);
      if (stor) {
        stor.forEach((e, i) => {
          if (id == e.id) {
            e.currentChapterIndex = this.currentChapterIndex;
            window.console.log(this.currentChapterIndex);
          }
        });
      localStorage.setItem("bookcase", JSON.stringify(stor));
      }

    },
    //获取 列数
    getColumnCount() {
      window.console.log(this.$refs.content);
      window.console.log(this.$refs.content.clientHeight);
      this.th = this.$refs.content.clientHeight;
      let h = 580;

      this.columnCount = Math.ceil(this.th / h);
      window.console.log(this.columnCount);
    },
    // 翻页
    turnPage(e) {
      this.getColumnCount();
      // 每一次翻页偏移量 15为左边内边距
      let px = window.innerWidth - 25;
      window.console.log(px);
      // 如果点击右边 下一页 如果点击左边 上一页 上一页前判断当前的translateX的值 下一页呢？
      window.console.log(e.pageX, window.innerWidth / 2);
      if (e.pageX > window.innerWidth / 2) {
        // 在右
        this.currentColumn += 1;
      } else {
        this.currentColumn = this.currentColumn <= 0 ? 0 : --this.currentColumn;
      }
      if (this.currentColumn > this.columnCount - 1) {
        this.nextChapters();
        window.console.log("最后一页");
      }
      if (this.currentColumn <= 0) {
        window.console.log("已经是第一页了");
      }
      this.currentX = px * this.currentColumn;
      window.console.log(this.currentX);
    },
    // changeLight(value){
    //   window.console.log(value)
    //   let x = (100-value)/100
    //   this.brightness = "rgba(0,0,0,"+x+")"
    // },
    toNight() {
      this.isNight = !this.isNight;
    },
    minus() {
      this.textsize -= 2;
      if (this.textsize <= 12) {
        this.textsize = 12;
      }
      this.fontSize = this.textsize + "px";
      this.lineheight = this.textsize + 2 + "px";
    },
    plus() {
      this.textsize += 2;
      if (this.textsize >= 30) {
        this.textsize = 30;
      }
      this.fontSize = this.textsize + "px";
      this.lineheight = this.textsize + 2 + "px";
    },
    showCatalogue() {
      this.showcatalogue = true;
    },
    showSet() {
      this.showset = true;
    },
    changeChapter(link, index = this.currentChapterIndex) {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      this.currentChapterIndex = index;
      this.axios
        .get("https://novel.kele8.cn/chapters/" + encodeURIComponent(link))
        .then(res => {
          window.console.log(res);
          this.chapter = res.data.chapter;
          this.showcatalogue = false;
          // this.showBar = false
          this.currentColumn = 0;
          this.currentX = 0;
          this.showBar = true;
          this.$toast.clear();
        });
    },
    //下一章
    nextChapters() {
      //边界要判断
      this.currentChapterIndex += 1;
      if (this.currentChapterIndex > this.chapters.length - 1) {
        window.console.log("已经是最后一章");
        return;
      }
      this.changeChapter(this.chapters[this.currentChapterIndex].link);
    }
  },
  beforeDestroy() {
    this.saveCurrentChapterIndex();
  },
  beforeRouteLeave(to, from, next) {
    // ...
    this.saveCurrentChapterIndex();
    this.showBar = true;
    next();
  },
  beforeRouteEnter(to, from, next) {
    // ...

    // var battery = navigator.battery || navigator.webkitBattery || navigator.mozBattery;
    window.console.log(navigator);

    window.console.log(to);
    let { id } = to.query;
    let link = encodeURIComponent(to.query.link);
    window.console.log(link);
    Vue.axios.get("https://novel.kele8.cn/chapters/" + link).then(res => {
      next(vm => {
        vm.$toast.clear();
        window.console.log(res);
        vm.chapter = res.data.chapter;
        vm.chapters = vm.$store.state.chapters;
        vm.bookId = id;
        vm.$nextTick(() => {
          vm.getColumnCount();
        });
      });
    });
  }
};
</script>

<style lang="scss" scoped>
.read-page {
  height: 100vh;
  background: #f2eeeb;
  position: relative;
  .topbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 46px;
    line-height: 46px;
    background-color: #fdf9f6;
    z-index: 3;
    display: flex;
    transition: top 0.6s;
    &.hide {
      top: -46px;
    }
    .topbar-left {
      flex: 1;
      .van-icon {
        vertical-align: middle;
        height: 46px;
        line-height: 46px;
        margin-right: 10px;
      }
      span {
      }
    }
    .topbar-right {
      width: 60px;
    }
  }
  .novel-top {
    background: #f2eeeb;
    color: #686868;
    position: sticky;
    top: 0;
    left: 0;
    width: 100vw;
    height: 5vh;
    box-sizing: border-box;
    padding: 10px 10px 0;
    z-index: 2;
  }
  .scroll-list-wrap {
    div.novel-content {
      font-family: "正楷";
      letter-spacing: 1px;
      color: #686868;
      font-size: 16px;
      margin-top: 0;
      // white-space: pre-wrap; /*css-3*/
      // white-space: -moz-pre-wrap; /*Mozilla,since1999*/
      // white-space: -pre-wrap; /*Opera4-6*/
      // white-space: -o-pre-wrap; /*Opera7*/
      // word-wrap: break-word; /*InternetExplorer5.5+*/
      background: #f2eeeb;
      //应该还是要截取分段 否则无法缩进

      padding: 5px 10px;
      p {
        text-align: left;
        text-indent: 2em;
        // padding-bottom: 10px;
        line-height: 24px;
      }
    }
  }
  div.novel-content {
    font-family: "正楷";
    letter-spacing: 1px;
    color: #686868;
    font-size: 16px;
    margin-top: 0;
    background: #f2eeeb;
    padding: 0px 15px;
    //翻页模式
    overflow: hidden;
    height: 95vh;
    // column-width: 100vw;
    // column-fill: balance;
    position: relative;
    .midBtn {
      width: 25vw;
      height: 50vw;
      // border: 1px solid red;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -12.5vw;
      margin-top: -25vw;
    }
    // 每一列间隔
    column-gap: 15px;
    .turn {
      padding: 10px;
      display: block;
      column-count: 1;
      column-gap: 25px;
      width: auto;
      height: 580px;
      font-size: 16px;
      font-weight: bolder;
      cursor: move;
      transition-duration: 0.5s;
    }
    p {
      //每个段落头缩进
      text-align: left;
      text-indent: 2em;
      // padding-bottom: 10px;
      line-height: 24px;
      pointer-events: none;
    }
  }
  .read-page-bottom-bar {
    transition: bottom 0.6s;
    height: 54px;
    background: #fdf9f6;
    &.hide {
      bottom: -55px;
    }
  }
  &.night {
    .topbar {
      background-color: #141414;
      color: #686868;
      .topbar-left {
        .van-icon {
        }
        span {
        }
      }
      .topbar-right {
      }
    }
    .novel-top {
      background: #141414;
      color: #686868;
    }
    .scroll-list-wrap {
      div.novel-content {
        background: #141414;
      }
    }
    .read-page-bottom-bar {
      background-color: #141414;
      color: #686868;
      border-top: none;
    }
  }
  .set {
    box-sizing: border-box;
    padding: 10px;
    //光亮设置
    .light {
      display: flex;
      height: 30px;
      .small {
        width: 20px;
        height: 20px;
        padding: 5px;
      }
      .van-slider {
        position: relative;
        top: 10px;
        flex: 1;
        height: 4px;
      }
      .big {
        width: 30px;
        height: 30px;
      }
    }
    //字体大小设置
    .textsize {
      padding: 5px;
      display: flex;
      button {
        outline: none;
        border: none;
        border-radius: 15px;
        color: #686868;
        background: transparent;
        border: 1px solid #686868;
      }
      .minus {
        width: 100px;
      }
      .van-stepper {
        flex: 1;
        text-align: center;
      }
      .plus {
        width: 100px;
      }
    }
    //颜色设置
    .color {
      padding: 5px;
      .van-col {
        div {
          height: 30px;
          border: 1px solid #a09d9d;
          border-radius: 4px;
        }
        &:nth-child(1) {
          div {
            background: #f7f1de;
          }
        }
        &:nth-child(2) {
          div {
            background: #f4deb5;
          }
        }
        &:nth-child(3) {
          div {
            background: #dbc8a8;
          }
        }
        &:nth-child(4) {
          div {
            background: #d1f0d0;
          }
        }
        &:nth-child(5) {
          div {
            background: #ffe7d9;
          }
        }
      }
    }
    // 行高设置
    .lineheight {
      .van-col {
        text-align: center;
        img {
          width: 30px;
          height: 30px;
        }
      }
    }
    //字体设置
    .fontfamily {
      padding: 5px;
      .van-col {
        div {
          text-align: center;
          height: 24px;
          line-height: 24px;
          border: 1px solid #a09d9d;
          border-radius: 4px;
        }
      }
    }
  }
  .van-overlay {
    z-index: 9999 !important;
    pointer-events: none;
    background-color: rgb(0, 0, 0);
    .wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
    }

    // .block {
    //   width: 120px;
    //   height: 120px;
    //   background-color: #fff;
    // }
  }
}
//目录
.chapter-item.active {
  color: red;
}
.scroll-list-wrap {
  height: 350px;
  overflow: visible;
  .cube-scroll-wrapper {
    overflow: visible;
  }
}
</style>
