          </cube-scroll>
        </div>
      </van-tab>
      <van-tab class="topbar female" title="女频">
        <van-sidebar v-model="femaleActiveKey">
          <cube-scroll ref="scroll">
            <van-sidebar-item
              v-for="(item, index) in female"
              :key="index"
              :title="item.shortTitle"
            />
          </cube-scroll>
