          </cube-scroll>
        </div>
      </van-tab>
      <van-tab class="topbar press" title="出版">
        <van-sidebar v-model="epubActiveKey">
          <cube-scroll ref="scroll">
            <van-sidebar-item v-for="(item, index) in epub" :key="index" :title="item.shortTitle" />
