    // ...
    Vue.$myGet("ranking", 1000, "https://novel.kele8.cn/rank-category").then(
      res => {
        next(vm => {
          window.console.log(res, vm);
          //拿到排行分类
          vm.ranking = res.data;
          //遍历每一项male,female,epub
          vm.ranking.male.forEach((e, i, males) => {
            vm.axios.get(vm.$origin + "/rank/" + e._id).then(res => {
