<template>
  <div class="classifyDetail">
    <TopBar :title="$route.query.major"></TopBar>
    <div class="tags">
      <!-- <div class="active">全部</div>
    <div>东方玄幻</div>
    <div>异界大陆</div>
    <div>异界争霸</div>
      <div>远古神话</div>-->
      <template>
        <div v-for="(item,index) in mins" :key="index">
          <div :class="{'active':num==index}" @click="switchTag(index,item)">{{item}}</div>
        </div>
      </template>
    </div>

    <div class="search-detail-list">
      <div
        class="search-detail-list-item"
        v-for="(item,index) in books"
        :key="index"
        @click="toBookDetail(item._id)"
      >
        <div class="search-detail-list-item-image">
          <img
            style="width:75px;height:100px;"
            v-lazy="'https://statics.zhuishushenqi.com'+item.cover"
          />
        </div>
        <div class="search-detail-list-item-info">
          <div class="title">{{ item.title }}</div>
          <div class="shortIntro">{{ item.shortIntro }}</div>
          <div class="bottom">
            <div class="bottom-left">
              <!-- <van-icon name="user-o" /> -->
              <span class="author">{{ item.author }}</span>
              <span class="follower">{{item.latelyFollower}}人气</span>
            </div>
            <div class="bottom-right">
              <span>{{ item.retentionRatio }}%</span>留存
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import TopBar from "../components/TopBar";
export default {
  data() {
    return {
      num: 0,
      mins: null,
      books: [],
      gender: "",
      major: "",
      minor: "",
      page: 0
    };
  },
  components: {
    TopBar
  },
  methods: {
    toBookDetail(id) {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      this.$router.push({ path: "bookdetail", query: { bookId: id } });
    },
    switchTag: function(index, minor) {
      window.console.log("切换标签");
      this.num = index;
      this.minor = minor;
      if (minor == "全部") {
        this.getClassifyDetail(this.gender, this.major);
      } else {
        this.getClassifyDetail(this.gender, this.major, minor);
      }
    },
    getClassifyDetail(gender, major, minor = "", page = 0) {
      this.axios
        .get("https://novel.kele8.cn/category-info", {
          params: {
            gender: gender,
            type: "hot",
            major: major,
            minor: minor,
            start: page * 20,
            limit: 20
          }
        })
        .then(res => {
          window.console.log(res);
          this.books = res.data.books;
          //   (this.page = page), (this.books = res.data.books);
        });
    }
    // getClassifyDetailNextPage(gender, major, minor = "", page = 0) {
    //   this.axios
    //     .get(this.$origin + "category-info", {
    //       params: {
    //         gender: gender,
    //         type: "hot",
    //         major: major,
    //         minor: minor,
    //         start: page * 20,
    //         limit: 20
    //       }
    //     })
    //     .then(res => {
    //       window.console.log(res)((this.page = page)),
    //     //     (this.books = res.data.books);
    //     //   this.page = this.page + 1;
    //     //   this.books = this.books.concat(res.data.books);
    //     });
    // }
  },
  beforeRouteEnter(to, from, next) {
    // ...
    window.console.log(to);
    let { major, gender } = to.query;
    // 根据父分类,获取子分类
    Vue.axios.get("https://novel.kele8.cn/sub-categories").then(res => {
      next(vm => {
        vm.$nextTick(() => {
          window.console.log(res);
          if (gender == "male") {
            let arr = res.data.male;
            arr.forEach((v, i) => {
              if (v.major == major) {
                let mins = v.mins;
                mins.unshift("全部");
                vm.mins = mins;
              }
            });
          }
          vm.gender = gender;
          vm.major = major;
          vm.num = 0
          vm.getClassifyDetail(gender, major);
        });
      });
    });
  }
};
</script>

<style>
/* pages/classifyDetail/classifyDetail.wxss */
.tags {
  overflow: hidden;
  display: flex;
}
.tags div {
  /* float: left; */
  flex: 1;
  margin: 5px;
  padding: 3px;
  white-space: nowrap;
  font-size: 12px;
  text-align: center;
  color: #89888d;
  box-sizing: border-box;
}
.tags div.active {
  border: 1px solid #f2826a;
  color: #f2826a;
  border-radius: 5px;
}

/* 内容 */
.search-detail-list {
  padding: 0 10px;
}
.search-detail-list-item {
  padding: 10px 0;
  display: flex;
}
.search-detail-list-item-image {
  text-align: center;
  width: 70px;
}
.search-detail-list-item-image image {
  width: 100%;
  box-shadow: 4px 5px 6px -2px rgba(0, 0, 0, 0.4);
}
.search-detail-list-item-info {
  flex: 1;
  margin-left: 10px;
  height: 100px;
  color: #646566;
  display: flex;
  flex-direction: column;
}
.title {
  font-weight: 500;
  color: #222;
  line-height: 32px;
  flex: 1;
}
.shortIntro {
  font-size: 12px;
  height: 36px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 2;
}
.bottom {
  /* height: 28px; */
  line-height: 32px;
  font-size: 12px;
  display: flex;
}
.bottom .bottom-left {
  margin: 0;
  flex: 1;
}
.author {
  margin-right: 5px;
}
.follower {
  color: #db2016;
}
.bottom .bottom-right {
  /* border: 1px solid #000; */
  /* padding: 10px 5px; */
  /* float: right; */
  flex: 1;
  font-size: 10px;
  color: gray;
  /* margin-top: 2px; */
}
.bottom .bottom-right div {
  color: #db2016;
  margin-right: 5px;
}
</style>