<template>
  <div class="bookcase">
    <van-nav-bar left-text="轻读">
      <!-- <div slot="right">
        <van-icon name="search" color="white" @click="$router.push({ path: '/searchpage' })" />
        <van-icon name="ellipsis" color="white" @click="$router.push({ path: '/bookcity' })" />
      </div>-->
    </van-nav-bar>
    <div class="bookcase-list">
      <!-- 书籍简介 -->
      <div v-for="(datas,index) in books" v-bind:key="index">
        <section class="book-brief">
          <img v-lazy="datas.bookDetails.cover" />
          <div class="desc">
            <p class="title">{{datas.title}}</p>
            <p class="msg">
              <span class="author">{{datas.bookDetails.author}}&nbsp;</span>
              |
              <span>&nbsp;{{datas.bookDetails.cat}}&nbsp;</span>
            </p>
            <p class="count">
              <span>章节数：{{datas.bookDetails.chaptersCount}}&nbsp;</span>
              |
              <span>&nbsp;{{datas.bookDetails.wordCount}}字</span>
            </p>
            <!-- <p class="catalog">
                <span>当前阅读至：第{{catalog}}章</span>
            </p>-->
            <p class="catalog">
              <span>当前阅读至：第{{datas.currentChapterIndex+1}}章</span>
            </p>
          </div>
          <div class="btns" v-if="books">
            <p @click="removeFromBookcase(datas.id)">撤出</p>
            <p class="read" @click="toReadPage(datas.id,datas.title,datas.currentChapterIndex)">阅读</p>
          </div>
        </section>
      </div>
      <template v-if="books&&!books.length">
        <div class="none">
          <p>还没有书籍，去书城逛逛吧</p>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      books: null
    };
  },
  methods: {
    toReadPage(id, title,currentChapterIndex) {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      //拿书源id
      this.axios
        .get(this.$origin + "/book-sources?view=summary&book=" + id)
        .then(res => {
          window.console.log(res.data[0]._id);
          //根据书源id拿章节列表
          this.axios
            .get(this.$origin + "/book-chapters/" + res.data[0]._id)
            .then(response => {
              window.console.log(response);
              let { chapters } = response.data;
              this.$store.commit("setChapters", chapters);
              //默认第一章
              let { link } = chapters[currentChapterIndex];
              this.$router.push({
                path: "readpage",
                query: { link: link, title: title, id: id }
              });
            });
        });
      // this.$router.push({path:'readpage',query:{link:link,title:this.title}})
    },
    //移出书架的方法
    removeFromBookcase(id) {
      window.console.log("要移除的书籍id", id);
      var stor = JSON.parse(localStorage.getItem("bookcase"));
      stor.forEach((e, i, arr) => {
        if (e.id == id) {
          window.console.log("找到该书籍并删除");
          arr.splice(i, 1);
          window.console.log(i, arr);
          localStorage.setItem("bookcase", JSON.stringify(arr));
          this.books = arr;
        }
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    // ...
    next(vm => {
      vm.books = JSON.parse(localStorage.getItem("bookcase"));
    });
  },
  created() {
    window.console.log("书架");
  },
  mounted() {
    window.console.log(this.books);
  }
};
</script>

<style lang="scss">
.bookcase-list {
  padding: 0 15px;
}
.book-brief {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100px;
  padding: 10px 0;
  img {
    flex: 0 0 60px;
    display: block;
    width: 60px;
    height: 100%;
  }
  .desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
    width: 100%;
    padding-left: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    p {
      font-size: 12px;
      line-height: 25px;
      color: #999;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .title {
      height: 25px;
      font-size: 16px;
      font-weight: 400;
      color: #222;
    }
    .msg {
      height: 25px;
      .author {
        color: #b93321;
      }
    }
  }
  .btns {
    flex: 0 0 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80px;
    height: 80px;
    padding-left: 10px;
    text-align: center;
    p {
      padding: 5px;
      border: 1px solid #b93321;
      border-radius: 2px;
      font-size: 16px;
      text-align: center;
      line-height: 20px;
      color: #b93321;
    }
    .read {
      color: #fff;
      background-color: #b93321;
    }
  }
}
.none {
  width: 100%;
  height: 60px;
  padding: 20px 10px;
  p {
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #ccc;
  }
}
</style>
