<template>
  <div class="book-list-item">
    <van-row type="flex" @click="toBookDetail(book._id)">
      <van-col span="6" class="book-item-img">
        <img style="width:75px;height:100px" v-lazy="'https://statics.zhuishushenqi.com'+book.cover" />
      </van-col>
      <van-col span="18" class="book-item-info">
        <div class="book-item-info-title">{{ book.title }}</div>
        <div class="book-item-info-shortIntro">{{ book.shortIntro }}</div>
        <div class="book-item-info-bottom">
          <span class="book-item-info-bottom-author"
            ><van-icon name="manager-o" />{{ book.author }}</span
          >
          <van-tag round color="#f2826a" class="book-item-info-bottom-tag latelyFollower"
            >{{ translatePlayCount(book.latelyFollower) }}人气</van-tag
          >
          <van-tag round class="book-item-info-bottom-tag">{{ book.majorCate }}</van-tag>
        </div>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  props: ["book"],
  data(){
    return {
    }
  },
  mounted() {
    window.console.log(this.book)
  },
  watch: {
    // book() {
    //   if (this.book) {
    //       //slice(7)过滤掉/agent/   unescape对图片进行解码
    //       // this.book.cover = unescape(this.book.cover.slice(7));
    //       this.book.cover = "https://statics.zhuishushenqi.com/"+escape(this.book.cover);
    //   }
    // },
  },
  methods: {
    translatePlayCount(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(2) + "亿";
      } else if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      } else {
        return n;
      }
    },
    toBookDetail(id) {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      this.$router.push({ path: "bookdetail", query: { bookId: id } });
    },
  },
};
</script>

<style lang="scss">
@mixin oneline() {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.book-list-item {
  .van-row {
    // height: 150px;
    padding: 10px 0 0 0;
    .book-item-img {
      text-align: center;
      .van-image {
        box-shadow: 0 0 12px -2px rgba(0, 0, 0, 0.4);
      }
    }
    .book-item-info {
      margin-left: 10px;
    }
    .van-col.book-item-info {
      height: 100px;
      .book-item-info-title {
      }
      .book-item-info-shortIntro {
        font-size: 12px;
