<template>
  <div class="search-bar" @click="$router.push({path:'/find'})">
    <van-search  />
    <van-swipe
      class="search-swipe"
      :autoplay="5000"
      :height="24"
      :initial-swipe="currentHotWordsIndex"
      vertical
      :touchable="false"
      :show-indicators="false"
      @change="getCurrentIndex"
    >
      <van-swipe-item v-for="(item, index) in $store.state.hotWords" :key="index">{{ item }}</van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentHotWordsIndex:0
    };
  },
  methods: {
      gethotWords(){
          this.$myGet("hot-books",60,this.$origin+"/hot-books").then(res=>{
              window.console.log(res);
              this.$store.commit('setHotWords', res.data.hotWords)
              this.$store.commit('setNewHotWords', res.data.newHotWords)
          })
          this.$myGet("search-hotwords",60,this.$origin+"/search-hotwords").then(res=>{
              window.console.log(res);
              this.$store.commit('setSearchHotWords', res.data.searchHotWords)
          })
      },
      getCurrentIndex(i){
        window.console.log(i)
        this.$store.commit('setHotWordsIndex', i)
      }
  },
  created() {
      this.gethotWords()
      this.currentHotWordsIndex = this.$store.state.hotWordsIndex
  },
};
</script>

<style lang='scss'>
.search-bar{
    position: relative;
    .van-swipe{
        position: absolute;
        top: 15px;
        left: 41px;
        width: 80%;
        height: 24px;
        pointer-events: none;
        .van-swipe-item{
            line-height: 24px;
            text-align: left;
            color: #646566;
        }
    }
}
</style>
