<template>
  <div class="book-item" v-if="books">
    <!-- layout布局 左边6图片 右边18详情 -->
    <van-row type="flex" @click="toBookDetail(books[0]._id)">
      <van-col span="6" class="book-item-img">
        <!-- <van-image width="75" height="100"  v-lazy="books[0].cover" /> -->
        <img v-lazy="books[0].cover" />
      </van-col>
      <van-col span="18" class="book-item-info">
        <div class="book-item-info-title">{{ books[0].title }}</div>
        <div class="book-item-info-shortIntro">{{ books[0].shortIntro }}</div>
        <div class="book-item-info-bottom">
          <span class="book-item-info-bottom-author"
            ><van-icon name="manager-o" />{{ books[0].author }}</span
          >
          <van-tag round color="#f2826a" class="book-item-info-bottom-tag latelyFollower"
            >{{ translatePlayCount(books[0].latelyFollower) }}人气</van-tag
          >
          <van-tag round class="book-item-info-bottom-tag">{{ books[0].majorCate }}</van-tag>
        </div>
      </van-col>
    </van-row>
    <!-- grid宫格  -->
    <van-grid>
      <van-grid-item v-for="(item, index) in someBooks" :key="index" @click="toBookDetail(item._id)">
        <!-- <van-image v-lazy="item.cover" /> -->
        <img v-lazy="item.cover">
        <template slots="text">
          <span>{{ item.title }}</span>
        </template>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
export default {
  props: ["type"],
  data() {
    return {
      books: null
    };
  },
  watch: {
  books(){
    this.books.forEach(e => {
        e.cover = unescape(e.cover.slice(7));
      });
  }
  },
  computed: {
    someBooks() {
      return this.books.slice(1, 5);
    }
  },
  mounted() {},

  methods: {
    toBookDetail(id) {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      this.$router.push({ path: "bookdetail", query: { bookId: id } });
    },
    translatePlayCount(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(2) + "亿";
      } else if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      } else {
        return n;
      }
    }
  },
  created() {
    this.$nextTick(() => {
      window.console.log(this.type);
      if (this.type === 0) {
        this.books = this.$store.state.jipin;
      }
      if (this.type === 1) {
        this.books = this.$store.state.renqi;
      }
      if (this.type === 2) {
        this.books = this.$store.state.rexue;
      }
      if (this.type === 3) {
        this.books = this.$store.state.xiaozhong;
      }
    });
  }
};
</script>

<style lang="scss">
@mixin oneline() {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.book-item {
  padding: 0 10px;
  .van-row {
    // height: 150px;
    padding: 10px 0 0 0;
    .book-item-img {
      text-align: center;
      img{
        width: 75px;
        height: 100px;
        box-shadow: 0 0 12px -2px rgba(0, 0, 0, 0.4);
      }
    }
    .book-item-info {
      margin-left: 10px;
    }
    .van-col.book-item-info {
      height: 100px;
      .book-item-info-title {
      }
      .book-item-info-shortIntro {
        font-size: 12px;
        height: 32px;
        margin: 10px 0;
        line-height: 16px;
        @include oneline();
        -webkit-line-clamp: 2;
        color: #7c7c7c;
        //   letter-spacing: 1px;
        font-family: "宋体";
      }
      .book-item-info-bottom {
        height: 28px;
        font-size: 12px;
        .book-item-info-bottom-author {
          margin: 0;
          color: #7c7c7c;
          line-height: 28px;
        }
        .book-item-info-bottom-tag {
          margin: 0;
          float: right;
          font-size: 10px;
          margin-top: 5px;
          &.latelyFollower {
          }
        }
      }
    }
  }
  .van-grid {
    .van-grid-item {
      img {
        width: 70px;
        height: 100px;
        box-shadow: 0 0 12px -2px rgba(0, 0, 0, 0.4);
        overflow: hidden;
      }
      span {
        @include oneline();
        -webkit-line-clamp: 1;
        font-size: 10px;
        line-height: 12px;
        text-align: left;
        margin-top: 8px;
      }
    }
  }
  
}
</style>
