import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  //state相当于data
  state: {
    hotWords: null,
    newHotWords: null,
    hotWordsIndex: 0,
    searchHotWords: null,
    jipin:null,
    renqi:null,
    rexue:null,
    xiaozhong:null,
    chapters:null
  },
  //getters相当于计算属性
  // getters:{
  //   donejipin(state){
  //     state.jipin.forEach(e => {
  //       e.cover = unescape(e.cover.slice(7));
  //     });
      
  //   },
  //   donerenqi(state){
  //     state.renqi.forEach(e => {
  //       e.cover = unescape(e.cover.slice(7));
  //     });
  //   },
  //   rexue(state){
  //     state.rexue.forEach(e => {
  //       e.cover = unescape(e.cover.slice(7));
  //     });
  //   },
  //   xiaozhong(state){
  //     state.xiaozhong.forEach(e => {
  //       e.cover = unescape(e.cover.slice(7));
  //     });
  //   }
    
  // },
  mutations: {
    setHotWords(state, hotWords) {
      state.hotWords = hotWords
    },
    setNewHotWords(state,newHotWords){
      state.newHotWords = newHotWords
    },
    setHotWordsIndex(state, index) {
      state.hotWordsIndex = index
    },
    setSearchHotWords(state, searchHotWords) {
      state.searchHotWords = searchHotWords
    },

    changeHotWords(state) {
      var spliceArr = state.newHotWords.splice(0, 6);
      spliceArr.forEach((e) => {
        state.newHotWords.push(e)
      })
    },
    setHighest(state,{data,name}){
      window.console.log(data,name)
      if(name=="jipin"){
        state.jipin = data
      }
      if(name=="renqi"){
        state.renqi = data
      }
      if(name=="rexue"){
        state.rexue = data
      }
      if(name=="xiaozhong"){
        state.xiaozhong = data
      }
    },
    setChapters(state,chapters){
      state.chapters = chapters
    }

  },
  actions: {},
  modules: {}
})