<template>
  <div class="home">
    <div class="wrapper" ref="wrapper">
      <div class="content">
          <keep-alive>
        <transition name="fade" mode="out-in">
            <router-view></router-view>
        </transition>
          </keep-alive>
      </div>
      <BookFooter />
    </div>
    <!-- <div class="empty"></div> -->
  </div>
</template>

<script>
import BookFooter from "../components/BookFooter";
import BScroll from "better-scroll";
export default {
  name: "home",
  components: {
    BookFooter
  },
  data() {
    return {
      currentPage: "",

      bScroll: null
    };
  },
  created() {
    this.currentPage = this.$route.name;
    // this.$nextTick(() => {
    //   this.scroll = new BScroll(this.$refs.wrapper, {
    //     scrollY: true,
    //     click: true
    //   });
    // });
  },
  mounted() {
    // window.console.log(this.$refs.wrapper)
    this.initBScroll();
  },
  watch: {
    $route(to, from) {
      window.console.log("to", to);
      window.console.log("from", from);

      // log("to",to);
      // log("from",from);
      this.currentPage = to.name;
    }
  },
  methods: {
    initBScroll() {
      if (this.bScroll) {
        this.bScroll.refresh();
      } else {
        this.bScroll = new BScroll(this.$refs.wrapper, {
          probeType: 3,
          click: true
        });
      }
    }
  }
};
</script>

