import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Bookcase from '../views/Bookcase.vue'
import Bookcity from '../views/Bookcity.vue'
import Find from '../views/Find.vue'
import SearchHotWordsPage from '../views/SearchHotWordsPage.vue'
import BookDetail from '../views/BookDetail.vue'
import Classify from '../views/Classify.vue'
import RankingPage from '../views/RankingPage.vue'
import Catalogue from '../views/Catalogue.vue'
import ReadPage from '../views/ReadPage.vue'
import ClassifyDetail from '../views/ClassifyDetail.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'home',
    component: Home,
    redirect: "/bookcity",
    children: [{
        path: '/bookcase',
        name: '书架',
        component: Bookcase
      },
      {
        path: '/bookcity',
        name: '书城',
        component: Bookcity
      },
      {
        path: '/find',
        name: '搜索',
        component: Find
      },
      {
        path: '/my',
        name: '我的',
        component: () => import('../views/My.vue')
      }
    ]
  },
  {
    path: '/searchpage',
    name: '搜索',
    component: () => import("@/views/SearchPage.vue")
  },
  {
    path: '/searchhotwordspage',
    name: '搜索热词',
    component: SearchHotWordsPage
  },
  {
    path: '/bookdetail',
    name: '书籍详情页',
    component: BookDetail
  },
  {
    path: '/classify',
    name: '分类',
    component: Classify
  },
  {
    path: '/rankingpage',
    name: '排行榜',
    component: RankingPage,

  },
  {
    path:'/catalogue',
    name:'目录',
    component:Catalogue
  },
  {
    path:'/readpage',
    name:'',
    component:ReadPage
  },
  {
    path:'/classifyDetail',
    component:ClassifyDetail
  },
  {
    path: '*',
    component: () => import('../components/NotFound.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router