<template>
  <div class="search-hot-words-page">
    <!-- 顶部bar -->
    <TopBar></TopBar>
    <!-- 列表 -->
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell
        v-for="(item, index) in list"
        :key="index"
        @click="$router.replace({ path: 'searchpage', query: { searchText: item.word } })"
      >
        <div slots="default">
          <span>{{ index }}</span>
          <span>{{ item.word }}</span>
        </div>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import TopBar from '@/components/TopBar.vue'
export default {
  components:{
    TopBar
  },
  data() {
    return {
      list: null,
      loading: false,
      finished: false
    };
  },
  methods: {
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        this.list = this.$store.state.searchHotWords;
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    },
    
  }
  
};
</script>

