<template>
  <div class="comment-item" v-if="comment">
    <div class="default">
      <div class="nick-info">
        <div class="pic"></div>
        <span>{{ comment.author.nickname }}</span>
        <van-tag plain round>lv.{{ comment.author.lv }}</van-tag>
      </div>
      <div class="score">
        <van-rate
          v-model="comment.rating"
          allow-half
          void-icon="star"
          void-color="#eee"
          size="10"
          :readonly="true"
        />
        <span>必看神作</span>
      </div>
      <div class="comment-content">
        {{ comment.content }}
      </div>
    </div>
    <!-- 短评没有 -->
    <div slot="footer" class="footer" v-if="comment.type !== 'short_review'">
      <div class="footer-left">
        {{ createdTime }}
      </div>
      <div class="footer-right">
        <van-icon name="comment-o" />{{ comment.commentCount }} <van-icon name="good-job-o" />{{
          comment.likeCount
        }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["comment"],
  data() {
    return {
      createdTime: null
    };
  },
  watch: {
    comment() {
      this.timeMaker(this.comment.created);
    }
  },
  created() {
      this.timeMaker(this.comment.created);
      
  },
  methods: {
    timeMaker(t) {
      var date = new Date(t); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var now = new Date();

      var Y = date.getFullYear();
      var M = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
      var D = date.getDate();
      var h = date.getHours();
      var m = date.getMinutes();
      var s = date.getSeconds();
      var NY = now.getFullYear();
      var NM = now.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
      var ND = now.getDate();
      var Nh = now.getHours();
      var Nm = now.getMinutes();
      var Ns = now.getSeconds();

      if (NY - Y > 0) {
        this.createdTime = NY - Y + "年前更新";
      } else if (NM - M > 0) {
        this.createdTime = NM - M + "月前更新";
      } else if (ND - D > 0) {
        this.createdTime = ND - D + "天前更新";
      } else if (Nh - h > 0) {
        this.createdTime = Nh - h + "小时前更新";
      } else if (Nm - m > 0) {
        this.createdTime = Nm - m + "分前更新";
      } else if (Ns - s > 0) {
        this.createdTime = Ns - s + "秒前更新";
      }
      // this.bookDetails.updated = Y + M + D + h + m + s;
    }
  }
};
</script>

<style></style>
